.category-nav {
  background: #f8f9fa;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1.5rem;
}

.category-nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.category-nav li {
  display: flex;
  align-items: center;
}

.category-nav button {
  background: white;
  border: 1px solid #dee2e6;
  border-radius: 6px;
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  color: #495057;
  cursor: pointer;
  transition: all 0.2s ease;
  white-space: nowrap;
}

.category-nav button:hover {
  background: #e9ecef;
  border-color: #ced4da;
}

.category-nav button.active {
  background: #007bff;
  color: white;
  border-color: #0056b3;
}

.category-nav button.active:hover {
  background: #0056b3;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .category-nav ul {
    flex-direction: column;
  }
  
  .category-nav li {
    width: 100%;
  }
  
  .category-nav button {
    width: 100%;
    text-align: left;
  }
} 